@use 'base/variables' as v; 
@use 'base/mixins' as m;

.header{
    background-color: v.$headerBg;
    @include m.tablet{
        &.fixed{
            position: fixed;
            left: 0;
            top: 0;
            width: 100%;
            box-shadow: .1rem .2rem .3rem #333;
        }
    }

    .header__content{
        padding: 2rem;
        @include m.tablet{
            display: flex;
            justify-content: space-between;
            align-items: center;

        }
   
    }

    .header__title{
        color:v.$white;
        text-align: center;
        @include m.tablet{
            margin: 0;
            text-align: left;
        }
    }
}

.header__links{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    @include m.tablet{
      flex-direction: row;
    }
    a{
        color: v.$white;
        font-size: 2.4rem;
        &:hover,
        &.active{
            color: v.$scndColor;
        }
    }
}



