@use 'variables' as v;

@mixin mobile{
    @media(min-width: v.$mobile){
        @content;
    }
}

@mixin tablet{
    @media(min-width: v.$tablet){
        @content;
    }
}


@mixin desktop{
    @media(min-width: v.$desktop){
        @content;
    }
}


@mixin desktopXL{
    @media(min-width: v.$desktopXL){
        @content;
    }
}

@mixin container{
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

@mixin grid($columns: 1, $gap: 5rem){
    display: grid;
    grid-template-columns:repeat($columns, 1fr);
    gap: $gap;
}

@mixin list__reset{
    margin: 0;
    padding: 0;
    list-style: none;
}



